import React, { useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import Img from 'gatsby-image';
import styled, { css } from 'styled-components';
import Media from 'react-media';

import { scrollRevealConfig } from '@config';
import { scrollReveal } from '@utils';
import { Icon, OutboundLink, Heading } from '@components';
import { mixins, media } from '@styles';

const tagMargin = '1.6rem';
const contentMobile = css`
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
`;

const WorksContainer = styled.section`
    ${mixins.homeSection};
    ${mixins.flexCenter};
    flex-direction: column;
    align-items: flex-start;
`;
const ContentContainer = styled.div`
    position: relative;
    ${media.bp800`
        ${contentMobile}
    `}
`;
const Role = styled.h3`
    font-size: 1.8rem;
    margin-bottom: 2rem;
    color: var(--color-text-primary-2);
    ${media.bp1040`
        margin-bottom: 1.6rem;
    `}
`;
const Duration = styled.h3`
    font-size: 1.5rem;
    margin-bottom: 1rem;
    color: var(--color-text-primary-2);
    ${media.bp1040`
        margin-bottom: 0.8rem;
    `}
`;
const CompanyName = styled.h5`
    font-size: 2.8rem;
    margin-bottom: 1rem;

    & a {
        color: var(--color-text-primary-1);
    }

    ${media.bp1040`
        margin-bottom: 1rem;
    `}
`;
const TechList = styled.ul`
    position: relative;
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    margin-bottom: 1rem;
    list-style: none;

    li {
        font-family: var(--fonts-mono);
        font-size: var(--font-size-xxs);
        margin-right: ${tagMargin};
        margin-bottom: 0.8rem;
        white-space: nowrap;
        color: var(--color-text-primary-2);

        &:last-of-type {
            margin-right: 0;
        }

        ${media.bp800`
            margin-bottom: 0.6rem;
        `}
    }

    ${media.bp800`
        margin-bottom: -1px;
    `}
`;

const IconLink = styled(props => <OutboundLink variant="styled-link" {...props} />)`
    &:hover,
    &:active {
        transform: none !important;
    }
`;
const WorkContainer = styled.div`
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    margin-bottom: 10rem;
    
    ${media.bp1280`
        grid-gap: 2.2rem;
    `}
    ${media.bp1040`
        grid-gap: 1.6rem;
        margin-bottom: 8rem;
    `}
    ${media.bp920`
        align-items:center; 
    `}
    ${media.bp800`
        ${contentMobile}
        margin-bottom: 6rem;
    `}

    &:last-of-type {
        margin-bottom: 0;
    }

    &:nth-of-type(odd) {
        ${ContentContainer} {            
            text-align: left;

            ${media.bp800`
                ${contentMobile}
                text-align:left;
            `}
        }
        ${TechList} {
            justify-content: flex-end;
            li {
                margin-left: ${tagMargin};
                margin-right: 0;
            }

            ${media.bp800`
                justify-content: flex-start;
                li {
                    margin-left: 0;
                    margin-right: ${tagMargin};
                }
            `}
        }
    }
`;

const Works = ({ data }) => {
    const headingRef = useRef();
    const projectRef = useRef([]);
    const imageRef = useRef();

    useEffect(() => {
        scrollReveal.reveal(headingRef.current, scrollRevealConfig());
        projectRef.current.forEach((ref, i) =>
            scrollReveal.reveal(ref, scrollRevealConfig(i * 100)),
        );
    }, []);

    return (
        <WorksContainer>
            <Heading id="work" ref={headingRef}>
                Work Experience
            </Heading>
            {data &&
                data.map(({ node }, i) => {
                    const { frontmatter, html } = node;
                    const { name, role, website, duration, responsibility } = frontmatter;

                    return (
                        <WorkContainer
                            key={`work-${i}`}
                            ref={el => {
                                projectRef.current[i] = el;
                            }}>
                            <ContentContainer>
                                <CompanyName>
                                    {name}
                                    {website && (
                                        <IconLink
                                            href={website}
                                            aria-label="website Link">
                                            <Icon name="External" />
                                        </IconLink>
                                    )}
                                </CompanyName>
                                <Role>{role}</Role>
                                <Duration>{duration}</Duration>
                                {/* <Description >
                                    {responsibility && (
                                        <ul>
                                            {responsibility.map((r, i) => (
                                                <li key={`tech-${i}`}>{r}</li>
                                            ))}
                                        </ul>
                                    )}
                                </Description> */}

                            </ContentContainer>

                        </WorkContainer>
                    );
                })}
        </WorksContainer>
    );
};

Works.propTypes = {
    data: PropTypes.array.isRequired,
};

export default Works;
